<!-- 专辑详情 -->
<template>
  <div class="introduce">
    <div class="intro-main">
      <div class="content">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>专辑</el-breadcrumb-item>
          <el-breadcrumb-item>{{basicInfo.specialName}}</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="bottom">
          <div class="cover">
            <div class="bg"></div>
          </div>
          <div class="info">
            <div class="title">{{basicInfo.specialName}}</div>
            <div class="auth">
              <span>{{basicInfo.userName}}</span>
              <span>|</span>
              <span>{{basicInfo.orgName}}</span>
            </div>
            <div class="basic-info">
              <span class="time">2019-10-20</span>
              <span>
                <i class="iconfont icon-solid-star" style="color:#FF8F1F"></i> {{basicInfo.avgScore}}
              </span>
              <span>
                <i class="iconfont icon-eyes"></i> {{basicInfo.visitCount}}
              </span>
              <!-- <span>
                <i class="iconfont icon-heart"></i>收藏
              </span> -->
            </div>
            <div class="intro">
              <span>
                简介
                <i class="el-icon-arrow-down"></i>
              </span>
              <p
                class="p-overflow"
              >{{basicInfo.specialMask}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="recommend">
      <div class="content">
        <div class="title">共计资源{{basicInfo.resourceCount}}</div>
        <div class="rec-lists">
          <res-box :info="item" v-for="item in resourseLists" :key="item.resourceId"></res-box>
        </div>
        <res-pagination 
          v-if="pagination.total/pagination.pageSize>1"
          @onSizeChange="handleSizeChange"
          :pageSize="pagination.pageSize"
          :currentPage="pagination.currentPage"
          :total="pagination.total">
        </res-pagination>
      </div>
    </div>

    <res-footer class="footer"></res-footer>
  </div>
</template>

<script>
export default {
  props:['id'],
  data(){
    return {
      basicInfo:{},
      resourseLists:[],
      pagination:{
        total:0,
        pageSize:20,
        currentPage:1
      }
    }
  },
  components: {
    resBox: () => import("./../../../components/res-box"),
    resFooter: () => import("@/components/res-footer"),
    resPagination: () => import("@/components/res-pagination")
  },
  created(){
    this.id&&this.initData()
    this.id&&this.getResource()
  },
  methods:{
    initData(){
      this.$api.specialsDataInfo(this.id).then(res => {
        this.basicInfo = res.data;
      })
    },
    getResource(){
      const params = {
        currPage:this.pagination.currentPage,
        pageSize:this.pagination.pageSize,
        specialId:this.id
      }
      this.$api.specialSelectResourceData(params).then(res => {
        this.resourseLists = res.data
        this.pagination.total = Number(res.totalDatas)
      })
    },
    handleSizeChange(currentPage) {
      this.pagination.currentPage = currentPage;
      this.getResource()
    }
  }
};
</script>

<style lang="scss" scoped>
.introduce {
  min-width: 1200px;
  overflow: hidden;
  height: 100%;
  .intro-main {
    background: #fff;
    padding-bottom: 30px;
    margin-top: 10px;
    .content {
      width: 80%;
      margin: 0 auto;
      padding-top: 30px;
      padding-bottom: 30px;
      .bottom {
        display: flex;
        margin-top: 30px;
        .cover {
          width: 520px;
          min-width: 520px;
          .bg {
            border-radius: 10px;
            width: 100%;
            height: 290px;
            background: url("./../../../assets/image/city-1.jpg") no-repeat;
            background-size: 100% 100%;
          }
        }
        .info {
          margin-left: 30px;
          width: 50%;
          .title {
            font-size: 22px;
            color: #333;
          }
          .auth {
            margin-top: 15px;
            span {
              color: #909399;
              line-height: 1;
              &:nth-child(2) {
                margin: 0 10px;
              }
            }
          }
          .basic-info {
            margin-top: 15px;
            span {
              color: #909399;
              margin-right: 30px;
            }
          }
          .intro {
            margin-top: 30px;
            position: relative;
            padding: 20px 15px 10px;
            border: 1px solid rgba(144, 147, 153, 0.2);
            border-radius: 4px;

            p {
              text-align: justify;
              line-height: 24px;
              min-height: 130px;
            }
            span {
              position: absolute;
              width: 4rem;
              background: #fff;
              top: -0.8rem;
              color: #909399;
              font-size: 16px;
              i {
                color: #909399;
              }
            }
          }
        }
      }
    }
  }

  .recommend {
    background: #f3f5f7;
    min-height: calc(100% - 485px);
    .content {
      .title {
        width: 80%;
        line-height: 22px;
        color: #333333;
        font-size: 16px;
        padding: 20px 0;
        margin: 0 auto;
      }
      .rec-lists {
        width: 81.6%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        & > div {
          width: 25%;
        }
        @media screen and (min-width: 1520px) {
          & > div {
            width: 20%;
          }
        }
      }
    }
  }

  .footer {
    background: #f3f5f7;
  }
}
</style>